
@include font("exo2-regular", exo2-regular);
@include font("pfdindisplaypro-black", pfdindisplaypro-black);
@include font("pfdindisplaypro-bold", pfdindisplaypro-bold);
@include font("pfdindisplaypro-light", pfdindisplaypro-light);
@include font("pfdindisplaypro-med", pfdindisplaypro-med);
@include font("pfdindisplaypro-reg", pfdindisplaypro-reg);
@mixin exo {
    font-family: "exo2-regular", Arial, sans-serif; }
@mixin pblack {
    font-family: "pfdindisplaypro-black", Arial, sans-serif; }
@mixin pbold {
    font-family: "pfdindisplaypro-bold", Arial, sans-serif; }
@mixin plight {
    font-family: "pfdindisplaypro-light", Arial, sans-serif; }
@mixin pmed {
    font-family: "pfdindisplaypro-med", Arial, sans-serif; }
@mixin preg {
    font-family: "pfdindisplaypro-reg", Arial, sans-serif; }


.out {
    background: #FFF;
    font-size: 16px;
    line-height: 1.4;
    @include plight; }
.h2 {
    text-align: center;
    display: inline-block;
    font-size: 32px;

    @include pmed;
    font-weight: normal;
    border-bottom: solid 4px #fea000; }
.h3 {
    display: inline-block;
    font-size: 24px;
    @include pbold;
    border-bottom: solid 4px #ff8a00;
    text-transform: uppercase; }
.h4 {
    @extend .h3;
    color: black;
    font-size: 22px; }
